@import url(https://fonts.googleapis.com/css2?family=Kanit&display=swap);
* {
  margin: 0;
  padding: 0;
  font-family: "Kanit", sans-serif;
}

.particles {
  position: fixed;
  background-color: slateblue;
}

.navbar {
  height: 80px;
  display: flex;
  justify-content: start;
  align-items: center;
  position: fixed;
}

.menu-bars {
  margin-left: 2rem;
  font-size: 2rem;
  background: none;
  cursor: pointer;
}

.nav-menu {
  background-color: darkslategray;
  width: 250px;
  height: 350px;
  display: flex;
  justify-content: center;
  position: fixed;
  z-index: 1000;
  top: 0;
  left: -100%;
  transition: 850ms;
  border-radius: 0px 0px 70px 5px;
  box-shadow: 10px 10px 4px rgba(0, 0, 0, 0.5);
}

.nav-menu.active {
  left: 0;
  transition: 350ms;
}

.nav-text {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
  color: #f5f5f5;
  cursor: pointer;
}

.nav-text a {
  text-decoration: none;
  color: #f5f5f5;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}

.nav-text a:hover {
  background-color: black;
}

.nav-menu-items {
  width: 100%;
}

.navbar-toggle {
  background-color: darkslategray;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: start;
  align-items: center;
}

span {
  margin-left: 16px;
}

.cube {
  width: 50%;
}

.slide {
  width: 100%;
  height: 50vh;
  margin-top: 20vh;
  margin-bottom: 10vh;
  border-style: solid;
}

.vid {
  position: absolute;
  top: 0%;
  z-index: -100px;
  width: 100%;
  height: 100%;
  overflow: hidden;
  opacity: 0.5;
}

.content {
  position: relative;
  z-index: 100;
  text-align: center;
  margin-bottom: 10%;
  margin-top: 10%;
  color: white;
}

p {
  margin-left: 10%;
  margin-right: 10%;
}

.btn {
  padding: 20px;
}

