.cube {
  width: 50%;
}

.slide {
  width: 100%;
  height: 50vh;
  margin-top: 20vh;
  margin-bottom: 10vh;
  border-style: solid;
}

.vid {
  position: absolute;
  top: 0%;
  z-index: -100px;
  width: 100%;
  height: 100%;
  overflow: hidden;
  opacity: 0.5;
}

.content {
  position: relative;
  z-index: 100;
  text-align: center;
  margin-bottom: 10%;
  margin-top: 10%;
  color: white;
}

p {
  margin-left: 10%;
  margin-right: 10%;
}

.btn {
  padding: 20px;
}
